
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ClientCoreComponent } from './client-core/client-core.component';

const routes: Routes = [
  {
    path: '',
    component: ClientCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'dashboard'
      },
      {
        path: 'dashboard',
        loadChildren: () => import('./client-core/client-dashboard/client-dashboard.module').then(m => m.ClientDashboardModule),
        data: {breadcrumb: 'CRM Client Dashboard'}
      },
      {
        path: 'accounts',
        loadChildren: () => import('./client-core/client-accounts/client-accounts.module').then(m => m.ClientAccountsModule),
        data: {breadcrumb: 'Accounts'}
      },
      {
        path: 'money',
        loadChildren: () => import('./client-core/fund-management/fund-management.module').then(m => m.FundManagementModule),
        data: {breadcrumb: 'Money'}
      },
      {
        path: 'reports',
        loadChildren: () => import('./client-core/reports/reports.module').then(m => m.ReportsModule),
        data: { breadcrumb: 'Reports'}
      },
      {
        path: 'platforms',
        loadChildren: () => import('./client-core/platforms/platforms.module').then(m => m.PlatformsModule),
        data: { breadcrumb: 'Platforms'}
      },
      {
        path: 'documents',
        loadChildren: () => import('./client-core/documents/documents.module').then(m => m.DocumentsModule),
        data: {breadcrumb: 'Documents'}
      },
      {
        path: 'profile',
        loadChildren: () => import('./client-core/profile/profile.module').then(m => m.ProfileModule),
        data: {breadcrumb: 'Profile'}
      },
      {
        path: 'trading-service',
        loadChildren: () => import('./client-core/trading-services/trading-services.module').then(m => m.TradingServicesModule),
        data: {breadcrumb: 'Trading-Service'}
      },
      {
        path: 'promotions',
        loadChildren: () => import('./client-core/promotions/promotions.module').then(m => m.PromotionsModule),
        data: {breadcrumb: 'Promotions'}
      },
      {
        path: 'risk-manager',
        loadChildren: () => import('./client-core/risk-manager/risk-manager.module').then(m => m.RiskManagerModule),
        data: {breadcrumb: 'Risk Manager'}
      },
      {
        path: 'affiliate',
        loadChildren: () => import('./client-core/affiliate/affiliate.module').then(m => m.AffiliateModule),
        data: {breadcrumb: 'Affiliate'}
      },
      {
        path: 'message-center',
        loadChildren: () => import('./client-core/message-center/message-center.module').then(m => m.MessageCenterModule),
        data: {breadcrumb: 'Message Center'}
      },
      {
        path: 'partner-area',
        loadChildren: () => import('./client-core/partner-area/partner-area.module').then(m => m.PartnerAreaModule),
        data: {breadcrumb: 'Partner Area'}
      },
      {
        path: 'economic-calendar',
        loadChildren: () => import('./client-core/economic-calendar/economic-calendar.module').then(m => m.EconomicCalendarModule),
        data: {breadcrumb: 'Economic Calendar'}
      },
      {
        path: 'signal-hub',
        loadChildren: () => import('./client-core/signal-hub/signal-hub.module').then(m => m.SignalHubModule),
        data: {breadcrumb: 'Signal Hub'}
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule { }
