import { HeaderService } from './../../../shared/services/header.service';
import { GlobalService } from './../../../shared/services/global.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from './../../../shared/guard/auth.service';
import { LayoutService } from '../../../shared/services/layout.service';
import { Component, OnInit, AfterViewInit, Input, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('navbarTypeAttr', {static: false}) navbarTypeAttr: ElementRef;
  @Input() deviceType: string;
  @Input() navLayout: string;
  @Input() defaultNavbar: string;
  @Input() toggleStatus: boolean;
  @Input() navbarEffect: string;
  @Input() toggleNavbar: string;
  @Input() headerColorTheme: string;
  @Input() collapsedHeader: boolean;
  public isSearchbarVisible = false;

  public walletBalance: any;
  public userName: string;
  public emailId: string;
  public profilePic: string;
  public profileData: any;
  subscription1: Subscription;
  subscription2: Subscription;

  constructor(
    private layoutService: LayoutService,
    private elRef: ElementRef,
    private authService: AuthService,
    private activateRoute: ActivatedRoute,
    private router: Router,
    private globalService: GlobalService,
    private headerService: HeaderService
  ) {
    this.headerService.getLoggedUserDetail();
    this.headerService.getUserWalletDetail();
  }

  ngOnInit(): void {
  }
  searchbarToggle() {
    this.isSearchbarVisible = !this.isSearchbarVisible;
  }
  requestFullscreen() {
    let ele;
    ele = document.documentElement;
    if (ele.requestFullscreen) {
      ele.requestFullscreen();
    } else if (ele.webkitRequestFullscreen) {
      ele.webkitRequestFullscreen();
    } else if (ele.mozRequestFullScreen) {
      ele.mozRequestFullScreen();
    } else if (ele.msRequestFullscreen) {
      ele.msRequestFullscreen();
    } else {
      console.log('Fullscreen API is not supported.');
    }
  }
  sidebarToggle() {
    this.layoutService.getToggleStatus();

  }
  ngAfterViewInit() {
    setTimeout (() => {
      this.loadDatas();
      this.userName = this.profileData.sProfile.uPersonalInfo.First;
      this.emailId = this.profileData.sProfile.uContactInfo.Email;
      this.profilePic = this.profileData.sProfile.uPersonalInfo.ProfilePic;
   }, 100);
  }

  // Logout from Application
  logout() {
    this.authService.logout();
    this.router.navigate(['/auth/login']);
  }
  loadDatas() {
    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      profileData => (this.profileData = profileData)
    );
    this.subscription2 = this.headerService.castWalletBalance.subscribe(
      resData => (this.walletBalance = resData)
    );
  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
    this.subscription2.unsubscribe();
  }
}
