import { Component, OnInit, HostListener, AfterViewInit } from '@angular/core';
import { LayoutService } from '../../shared/services/layout.service';

@Component({
  selector: 'app-client-core',
  templateUrl: './client-core.component.html',
  styleUrls: ['./client-core.component.scss']
})
export class ClientCoreComponent implements OnInit, AfterViewInit {
  setDeviceType: string;
  setNavLayout: string;
  setDefaultNavbar: string;
  setToggleStatus: boolean;
  setNavbarEffect: string;
  setAppWidth: string;
  setToggleNavbar: string;
  setHeaderColorTheme: string;
  setNavbarColorTheme: string;
  setContentSectionTheme: string;
  setCollapsedHeader: boolean;
  navbarTypeState: string;
  constructor(
    private layoutService: LayoutService,
  ) { }

  ngOnInit(): void {
    this.layoutService.checkWindowWidth(window.innerWidth);
    this.layoutService.castNavLayout.subscribe(
      navLayout => (this.setNavLayout = navLayout)
    );
    this.layoutService.castDeviceType.subscribe(
      deviceType => ( this.setDeviceType = deviceType)
    );
    this.layoutService.castdfNavbar.subscribe(
      defaultNavbar => (this.setDefaultNavbar = defaultNavbar)
    );
    this.layoutService.castToggleStatus.subscribe(
      toggleStatus => ( this.setToggleStatus = toggleStatus)
    );
    this.layoutService.castNavbarEffect.subscribe(
      navbarEffect => (this.setNavbarEffect = navbarEffect)
    );
    this.layoutService.castSetAppWidth.subscribe(
      appWidth => ( this.setAppWidth = appWidth)
    );
    this.layoutService.castToggleNavbar.subscribe(
      tNavbar => (this.setToggleNavbar = tNavbar)
    );
    this.layoutService.castHeaderTheme.subscribe(
      headerTheme => (this.setHeaderColorTheme = headerTheme)
    );
    this.layoutService.castNavbarTheme.subscribe(
      navbarTheme => (this.setNavbarColorTheme = navbarTheme)
    );
    this.layoutService.castContentSectionTheme.subscribe(
      contentTheme => (this.setContentSectionTheme = contentTheme)
    );
    this.layoutService.castCollapsedHeader.subscribe(
      collapsedHeader => ( this.setCollapsedHeader = collapsedHeader)
    );
    this.layoutService.castAsidebarState.subscribe(
      asidebarState => (this.navbarTypeState = asidebarState)
    );
  }
  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    const tw = event.target.innerWidth;
    if ( this.setDeviceType === 'desktop'  && tw < 1024 ) {
      this.layoutService.getVerticalNavbarOnWindowResize(event.target.innerWidth);
    } else if ( this.setDeviceType ===  'phone'  && tw > 768 ) {
      this.layoutService.getVerticalNavbarOnWindowResize(event.target.innerWidth);
    } else if (this.setDeviceType ===  'tablet' && tw < 768 ) {
      this.layoutService.getVerticalNavbarOnWindowResize(event.target.innerWidth);
    } else if ( this.setDeviceType ===  'tablet' && tw > 1024 ) {
      this.layoutService.getVerticalNavbarOnWindowResize(event.target.innerWidth);
    }


    // this.layoutService.getVerticalNavbarOnWindowResize(event.target.innerWidth);
  }
  sidebarToggle() {
    this.layoutService.getToggleStatus();
  }
  ngAfterViewInit() {
  }

}
