import { HandleScreenBarService } from './../../../shared/services/handle-screen-bar.service';
import { ScreenTitleService } from './../../../shared/services/screen-title.service';
import { Component, OnInit, Input, AfterViewInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-content-section',
  templateUrl: './content-section.component.html',
  styleUrls: ['./content-section.component.scss']
})
export class ContentSectionComponent implements OnInit, AfterViewInit {
  title: string;
  @Input() deviceType: string;
  @Input() navLayout: string;
  @Input() defaultNavbar: string;
  @Input() toggleStatus: boolean;
  @Input() navbarEffect: string;
  @Input() toggleNavbar: string;
  @Input() contentSectionTheme: string;
  public isScreenBarVisible = true;

  public isBreadcrumbVisible = false;
  public isDashboard: boolean;
  constructor(
    private screenTitle: ScreenTitleService,
    private handleScreenBarService: HandleScreenBarService

  ) {
    this.handleScreenBarService.castscreenTitlevisibility.subscribe(
      resData => (this.isScreenBarVisible = resData)
    );
  }
  ngOnInit(): void {
    this.screenTitle.castTitle.subscribe(
      title => ( this.title = title)
    );
  }
  ngAfterViewInit() {
  }
}
